.nav{
    position: fixed;
    top: 0;
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding: 20px;
    z-index: 1;
    height: 40px;
    /* Animations */
    transition-timing-function: ease-in;
    transition: all 0.5s;
}
.nav_logo{
    width: 90px;
    object-fit: contain;
    /* left: 0; */
    position: fixed;
    left: 20px;
    height: 50px;
}
.nav_black{
    background-color: black;
}