.banner{
    color: white;
    object-fit: contain;
    height: 440px;
}
.banner_contents{
    margin-left: 30px;
    padding-top: 140px;
    height: 190px;
}
.banner_title{
    font-size:3rem;
    font-weight: 800;
padding-bottom: 0.3rem;
}
.banner_description{
    width: 45rem;
    line-height: 1rem;
    padding-top: 1rem;
    font-size: 0.8rem;
    max-width: 360px;
    height: 80px;
}
.banner_button{
    cursor: pointer;
    color: rgb(213, 223, 231);
    background-color: rgba(51,51,51,0.5);
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 0.2vw;
    padding-left: 2rem;
    padding-right: 2rem;
    margin-right: 1rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
}
.banner_button:hover{
    color:black;
    background-color: rgb(229, 226, 226);
    transition: all 0.2s;
}
.banner--fadeBottom{
    height: 6.9rem;
    background-image: linear-gradient(
        180deg,
        transparent,
        rgba(37,37,37,0.61),
        #111
    );
}